@import '../../App.scss';

.countries-list {
  &__wrapper {
    padding-block: 32px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(264px, 1fr));
    grid-gap: 40px;
    justify-items: center;
  
    @include on-desktop {
      padding-block: 48px;
      grid-gap: 74px 74px;
    }
  }
}
